import { Component, OnInit } from '@angular/core';
import { User, Company } from 'src/app/models/models';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr'
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {

  public user: User;
  public company: Company;
  public emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  public roles: any[];

  constructor(
    private userService: UserService,
    private toastr: ToastrService) { }

  ngOnInit() {
    this.resetForm();
    this.userService.getAllRoles().subscribe((x: any) => {
      x.forEach(y => y.selected = false);
      this.roles = x;
    })
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }

    this.company = {
      CompanyId: 0,
      Name: '',
      Address1: '',
      Address2: '',
      City: '',
      State: '',
      Zip: ''
    };

    this.user = {
      UserName: '',
      Password: '',
      Email: '',
      FirstName: '',
      LastName: '',
      Roles: [],
      UserId: '',
      AgentLicenseNumber: '',
      Company: this.company
    }

    if (this.roles) {
      this.roles.map(x => x.selected = false);
    }
  }

  OnSubmit(form: NgForm) {
    var roles = ['Admin'];//this.roles.filter(x => x.selected).map(y => y.Name);

    const user: User = {
      UserName: form.value.UserName,
      Email: form.value.Email,
      Password: form.value.Password,
      FirstName: form.value.FirstName,
      LastName: form.value.LastName,
      Roles: ['Admin'],
      AgentLicenseNumber: form.value.AgentLicenseNumber,
      UserId: '',
      Company: {
        CompanyId: 0,
        Name: form.value.CompanyName,
        Address1: form.value.CompanyAddress1,
        Address2: form.value.CompanyAddress2,
        City: form.value.CompanyCity,
        State: form.value.CompanyState,
        Zip: form.value.CompanyZip
      }
    };

    this.userService.registerUser(user)
      .subscribe((data: any) => {
        if (data.Succeeded == true) {
          this.resetForm(form);
          this.toastr.success('User registration successful');
        }
        else
          this.toastr.error(data.Errors[0]);
      });
  }

  updateSelectedRoles(index) {
    this.roles[index].selected = !this.roles[index].selected;
  }

}


