import { Component, OnInit, Optional, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { StatusTrackerItem } from '../../status-tracker/status-tracker.component';

@Component({
  selector: 'app-dialog-status-test',
  templateUrl: './dialog-status-test.component.html',
  styleUrls: ['./dialog-status-test.component.scss']
})
export class DialogStatusTestComponent implements OnInit {

  ngOnInit(): void {
  }


  action:string;
  local_data:any;

  constructor(
    public dialogRef: MatDialogRef<DialogStatusTestComponent>,
    //@Optional() is used to prevent error if no data is passed
    @Optional() @Inject(MAT_DIALOG_DATA) public data: StatusTrackerItem) {
    console.log(data);
    this.local_data = {...data};
    this.action = this.local_data.action;
  }

  doAction(){
    this.dialogRef.close({event:this.action,data:this.local_data});
  }

  closeDialog(){
    this.dialogRef.close({event:'Cancel'});
  }


}
