import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { UserComponent } from './components/user/user.component';
import { SignUpComponent } from './components/user/sign-up/sign-up.component';
import { SignInComponent } from './components/user/sign-in/sign-in.component';
import { PdftesterComponent } from './components/pdftester/pdftester.component';
import { UserHierarchyComponent } from './components/admin/user-hierarchy/user-hierarchy.component';
import { AuthGuard } from './guard/auth.guard';
import { ForbiddenComponent } from './components/forbidden/forbidden.component';
import { StatusTrackerComponent } from './components/controls/status-tracker/status-tracker.component';
import { StatusBarComponent } from './components/test/status-bar/status-bar.component';
import { UserInviteComponent } from './components/admin/user-invite/user-invite.component';
import { UserInviteWelcomeComponent } from './components/admin/user-invite-welcome/user-invite-welcome.component';
import { CompanyManagementComponent } from './components/admin/company/company-management/company-management.component';
import { UserManagementComponent } from './components/admin/user-management/user-management.component';


const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'statustest', component: StatusBarComponent},


  { path: 'admin/userhierarchy', component: UserHierarchyComponent },
  { path: 'admin/CompanyManagement', component: CompanyManagementComponent },
  { path: 'admin/UserManagement', component: UserManagementComponent },
          
  { path: 'assets/pdfjs/web/viewer.html', redirectTo: 'scripts/lib/assets/pdfjs/web/viewer.html' },
  { path: 'assets', redirectTo: 'scripts/lib/assets' },
  
  { path: 'forbidden', component: ForbiddenComponent, canActivate: [AuthGuard] },

  { path: 'status', component: StatusTrackerComponent },
  
  { path: 'invite', component: UserInviteComponent,  canActivate:[AuthGuard]},
  { path: 'invite/welcome/:id', component: UserInviteWelcomeComponent},

  {
    path: 'signup', component: UserComponent,
    children: [{ path: '', component: SignUpComponent }]
  },
  {
    path: 'login', component: UserComponent,
    children: [{ path: '', component: SignInComponent }]
  },
  { path: 'pdftest', component: PdftesterComponent, canActivate:[AuthGuard] }

//  { path: '**', component: HomeComponent }
  //{ path: '',   redirectTo: '/home', pathMatch: 'full' }, // redirect to 

  
];
 
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
