import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-status-tracker',
  templateUrl: './status-tracker.component.html',
  styleUrls: ['./status-tracker.component.scss']
})
export class StatusTrackerComponent implements OnInit {

  @Input() model: StatusTrackerModel;

  constructor() { 
  }

  ngOnInit(): void {
  }

}

export class StatusTrackerModel{
  public header!: string;
  public items!: StatusTrackerItem[];
};

export class StatusTrackerItem{
  public label!: string;
  public state!: StatusTrackerState;
  public weight?: number = 10;
}

export enum StatusTrackerState{
  Complete = "status-tracker-complete",
  Active = "status-tracker-active",
  Alert = "status-tracker-alert",
  Pending = "status-tracker-pending"
}