import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Company, ApiResponse } from '../models/models';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  constructor(
    private http: HttpClient
  ) { }

  getCompanyByUser(){
    return this.http.get('api/Admin/Company');
  }
  
  getCompanies(): Observable<ApiResponse<Company[]>>{
    return this.http.get<ApiResponse<Company[]>>('api/Admin/Companies');
  }

  addCompany(company: Company): Observable<ApiResponse<Company>>{
    return this.http.post<ApiResponse<Company>>('api/Admin/Company', company);
  }

  updateCompany(company: Company): Observable<ApiResponse<Company>>{
    return this.http.put<ApiResponse<Company>>('api/Admin/Company', company);
  }

  deleteCompany(companyId: number){
    return this.http.delete(`api/Admin/Company?id=${companyId}`);
  }
  
}
