import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTable } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { DialogBoxComponent } from '../../controls/dialog-box/dialog-box.component';
import { StatusTrackerModel, StatusTrackerState, StatusTrackerItem } from '../../controls/status-tracker/status-tracker.component';
import { DialogStatusTestComponent } from '../../controls/dialog-boxes/dialog-status-test/dialog-status-test.component';
import {CdkDragDrop, moveItemInArray, transferArrayItem, CdkDropList} from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-status-bar',
  templateUrl: './status-bar.component.html',
  styleUrls: ['./status-bar.component.scss']
})
export class StatusBarComponent implements OnInit {

  displayedColumns: string[] = ['id', 'name', 'action'];
  displayedColumns1: string[] = ['label', 'state', 'weight', 'action'];
  dataSource = ELEMENT_DATA;
  dataSource1 = statusItems;

  public statusData: StatusTrackerModel; 

  @ViewChild(MatTable,{static:true}) table: MatTable<any>;
  @ViewChild('table1') table1: MatTable<any>;

  constructor(public dialog: MatDialog) {}
  ngOnInit(): void {
    this.statusData = 
      {header: 'hey1', items: this.dataSource1}
      ;
  }
 
  openDialog(action,obj) {
    obj.action = action;
    const dialogRef = this.dialog.open(DialogBoxComponent, {
      width: '250px',
      data:obj
    });
 
    dialogRef.afterClosed().subscribe(result => {
      if(result.event == 'Add'){
        this.addRowData(result.data);
      }else if(result.event == 'Update'){
        this.updateRowData(result.data);
      }else if(result.event == 'Delete'){
        this.deleteRowData(result.data);
      }
    });
  }
 
  addRowData(row_obj){
    var d = new Date();
    this.dataSource.push({
      id:d.getTime(),
      name:row_obj.name
    });
    this.table.renderRows();
    
  }
  updateRowData(row_obj){
    this.dataSource = this.dataSource.filter((value,key)=>{
      if(value.id == row_obj.id){
        value.name = row_obj.name;
      }
      return true;
    });
  }
  deleteRowData(row_obj){
    this.dataSource = this.dataSource.filter((value,key)=>{
      return value.id != row_obj.id;
    });
  }
 
  



  openDialog1(action,obj) {
    obj.action = action;
    const dialogRef = this.dialog.open(DialogStatusTestComponent, {
      width: '250px',
      data:obj
    });
 
    dialogRef.afterClosed().subscribe(result => {
      if(result.event == 'Add'){
        this.addRowData1(result.data);
      }else if(result.event == 'Update'){
        this.updateRowData1(result.data);
      }else if(result.event == 'Delete'){
        this.deleteRowData1(result.data);
      }
    });
  }
 
  addRowData1(row_obj){
    var d = new Date();
    this.dataSource1.push({
      label: row_obj.label,
      state: row_obj.state,
      weight: row_obj.weight
    });
    this.table.renderRows();
    
  }
  updateRowData1(row_obj){
    this.dataSource1 = this.dataSource1.filter((value,key)=>{
      if(value.label == row_obj.label){
        value.state = row_obj.state;
        value.weight = row_obj.weight;
      }
      return true;
    });
  }
  deleteRowData1(row_obj){
    this.dataSource1 = this.dataSource1.filter((value,key)=>{
      return value.label != row_obj.label;
    });
  }


}

export interface UsersData {
  name: string;
  id: number;
}


const statusItems: StatusTrackerItem[] = [
  {label: 'item1', state: StatusTrackerState.Complete},
  {label: 'item2', state: StatusTrackerState.Complete},
  {label: 'item3', state: StatusTrackerState.Complete},
  {label: 'item4', state: StatusTrackerState.Complete},
  {label: 'item5', state: StatusTrackerState.Complete, weight:60},
  {label: 'item6', state: StatusTrackerState.Pending},
  {label: 'item7', state: StatusTrackerState.Alert},
  {label: 'item8', state: StatusTrackerState.Active},
  {label: 'item9', state: StatusTrackerState.Complete}

];

const ELEMENT_DATA: UsersData[] = [
  {id: 1560608769632, name: 'Artificial Intelligence'},
  {id: 1560608796014, name: 'Machine Learning'},
  {id: 1560608787815, name: 'Robotic Process Automation'},
  {id: 1560608805101, name: 'Blockchain'}
];