import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { InviteWelcome, User, Company } from 'src/app/models/models';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-user-invite-welcome',
  templateUrl: './user-invite-welcome.component.html',
  styleUrls: ['./user-invite-welcome.component.scss']
})
export class UserInviteWelcomeComponent implements OnInit {

  public id: string;
  public inviterName: string;
  public inviteeRole: string;
  public user: User;
  public company: Company;
  public emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  public roles: any[];
  
  constructor( 
    private activatedRoute: ActivatedRoute,
    private userService: UserService,
    private toastr: ToastrService
    ) { }

  ngOnInit(): void {
    this.resetForm();

    this.id = this.activatedRoute.snapshot.paramMap.get("id");

    this.userService.getInvitedUser(this.id).subscribe((x:InviteWelcome)=> {
      this.inviterName = x.InviterName;
      this.inviteeRole = x.Role;
      
      this.user.UserName = x.InviteeEmail;
      this.user.Email = x.InviteeEmail;
      this.user.FirstName = x.InviteeFirstName;
      this.user.LastName = x.InviteeLastName;
      this.user.Roles = [x.Role];

      this.user.Company = x.Company
    });
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }

    this.company = {
      CompanyId: 0,
      Name: '',
      Address1: '',
      Address2: '',
      City: '',
      State: '',
      Zip: ''
    };

    this.user = {
      UserName: '',
      Password: '',
      Email: '',
      FirstName: '',
      LastName: '',
      Roles: [],
      UserId: '',
      AgentLicenseNumber: '',
      Company: this.company
    }

    if (this.roles) {
      this.roles.map(x => x.selected = false);
    }
  }

  OnSubmit(form: NgForm) {
    this.userService.registerUser(this.user, true)
      .subscribe((data: any) => {
        if (data.Succeeded == true) {
          this.resetForm(form);
          this.toastr.success('User registration successful');
        }
        else
          this.toastr.error(data.Errors[0]);
      });
  }

  updateSelectedRoles(index) {
    this.roles[index].selected = !this.roles[index].selected;
  }

}
