import { Component, OnInit, ViewChild } from '@angular/core';
import { User } from 'src/app/models/models';
import { MatTable } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { UserService } from 'src/app/services/user.service';
import { DialogUserEditComponent } from './dialog-user-edit/dialog-user-edit.component';

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.scss']
})
export class UserManagementComponent implements OnInit {

  displayedColumns: string[] = ['UserName', 'FirstName', 'LastName', 'Role', 'AgentLicenseNumber', 'Company', 'action'];
  dataSource: User[] = [];

  @ViewChild(MatTable,{static:true}) table: MatTable<any>;

  constructor(
    public dialog: MatDialog,
    private userService: UserService
    ) {}

  ngOnInit(): void {
    this.userService.getUsers().subscribe(x=>{
       if(x.IsSuccessful){
         this.dataSource = x.Data;
       }
    });
  }

  openDialog(action,obj) {
    obj.action = action;
    const dialogRef = this.dialog.open(DialogUserEditComponent, {
      width: '350px',
      data:obj
    });
 
    dialogRef.afterClosed().subscribe(result => {
      if(result.event == 'Update'){
        this.updateRowData(result.data);
      }else if(result.event == 'Delete'){
        this.deleteRowData(result.data);
      }
    });
  }
 

  //  this.userService.addUser(newRow).subscribe(x=>{
  //     if(x.IsSuccessful){
  //       this.dataSource.push(x.Data);
  //       this.table.renderRows();
  //     } else {
  //       //Bad User Add
  //     }
  //   });
    
  // }
  updateRowData(row: User){
    let foundUser: User | undefined = this.dataSource.find(x=>x.UserName === row.UserName)

    if(foundUser){
      foundUser.FirstName = row.FirstName;
      foundUser.LastName = row.LastName;
      foundUser.Roles = row.Roles;
      foundUser.AgentLicenseNumber = row.AgentLicenseNumber;
    }

    return this.userService.updateUser(foundUser).subscribe(x=>{
      if(x.IsSuccessful){
        console.log(x);
      }
    });
  }
  deleteRowData(row:User){
    this.dataSource = this.dataSource.filter((value,key)=>{
      return value.UserId != row.UserId;
    });

    return this.userService.deleteUser(row.UserId).subscribe(x=>console.log(x));
  }


}
