import { Component, OnInit, ViewChild } from '@angular/core';
import { Company } from 'src/app/models/models';
import { MatTable } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { DialogCompanyEditComponent } from '../dialog-company-edit/dialog-company-edit.component';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-company-management',
  templateUrl: './company-management.component.html',
  styleUrls: ['./company-management.component.scss']
})
export class CompanyManagementComponent implements OnInit {

  displayedColumns: string[] = ['Id', 'Name', 'Address1', 'Address2', 'City', 'State', 'Zip', 'action'];
  dataSource: Company[] = [];

  @ViewChild(MatTable,{static:true}) table: MatTable<any>;

  constructor(
    public dialog: MatDialog,
    private companyService: CompanyService
    
    ) {}

  ngOnInit(): void {
    this.companyService.getCompanies().subscribe(x=>{
      if(x.IsSuccessful){
        this.dataSource = x.Data;
      }
    });
  }

  openDialog(action,obj) {
    obj.action = action;
    const dialogRef = this.dialog.open(DialogCompanyEditComponent, {
      width: '350px',
      data:obj
    });
 
    dialogRef.afterClosed().subscribe(result => {
      if(result.event == 'Add'){
        this.addRowData(result.data);
      }else if(result.event == 'Update'){
        this.updateRowData(result.data);
      }else if(result.event == 'Delete'){
        this.deleteRowData(result.data);
      }
    });
  }
 
  addRowData(row){
    const newRow = {
      CompanyId: row.Id,
      Name: row.Name,
      Address1: row.Address1,
      Address2: row.Address2,
      City: row.City,
      State: row.State,
      Zip: row.Zip
    };



    this.companyService.addCompany(newRow).subscribe(x=>{
      if(x.IsSuccessful){
        this.dataSource.push(x.Data);
        this.table.renderRows();
      } else {
        //Bad Company Add
      }
    });
    
  }
  updateRowData(row: Company){
    let foundCompany: Company | undefined = this.dataSource.find(x=>x.CompanyId === row.CompanyId)

    if(foundCompany){
      foundCompany.Name = row.Name;
      foundCompany.Address1 = row.Address1;
      foundCompany.Address2 = row.Address2;
      foundCompany.City = row.City;
      foundCompany.State = row.State;
      foundCompany.Zip = row.Zip;
    }

    return this.companyService.updateCompany(foundCompany).subscribe(x=>{
      if(x.IsSuccessful){
        console.log(x);
      }
    });
  }
  deleteRowData(row:Company){
    this.dataSource = this.dataSource.filter((value,key)=>{
      return value.CompanyId != row.CompanyId;
    });

    return this.companyService.deleteCompany(row.CompanyId).subscribe(x=>console.log(x));
  }



}
