import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { SignalRModule } from 'ng2-signalr';
import { SignalRConfiguration } from 'ng2-signalr';
import { HomeComponent } from './components/home/home.component';
import { SignUpComponent } from './components/user/sign-up/sign-up.component';

import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ToastrModule } from 'ngx-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { UserService } from './services/user.service';

import { FormsModule } from '@angular/forms';
import { UserComponent } from './components/user/user.component';
import { SignInComponent } from './components/user/sign-in/sign-in.component';
import { MaterialModule } from './app.angular.material';
import { PdftesterComponent } from './components/pdftester/pdftester.component';

//import { PdfViewerModule } from 'ng2-pdf-viewer';
import { PdfJsViewerModule } from 'ng2-pdfjs-viewer';
import { UserHierarchyComponent } from './components/admin/user-hierarchy/user-hierarchy.component'; // <-- Import PdfJsViewerModule module
import { AuthGuard } from './guard/auth.guard';
import { AuthInterceptor } from './guard/auth.interceptor';
import { ForbiddenComponent } from './components/forbidden/forbidden.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { StatusTrackerComponent } from './components/controls/status-tracker/status-tracker.component';
import { StatusBarComponent } from './components/test/status-bar/status-bar.component';
import { DialogBoxComponent } from './components/controls/dialog-box/dialog-box.component';
import { DialogStatusTestComponent } from './components/controls/dialog-boxes/dialog-status-test/dialog-status-test.component';
import { UserAssociationComponent } from './components/admin/user-association/user-association.component';
import { UserInviteComponent } from './components/admin/user-invite/user-invite.component';
import { UserInviteWelcomeComponent } from './components/admin/user-invite-welcome/user-invite-welcome.component';
import { CompanyManagementComponent } from './components/admin/company/company-management/company-management.component';
import { DialogCompanyEditComponent } from './components/admin/company/dialog-company-edit/dialog-company-edit.component';
import { UserManagementComponent } from './components/admin/user-management/user-management.component';
import { DialogUserEditComponent } from './components/admin/user-management/dialog-user-edit/dialog-user-edit.component';

// >= v2.0.0
export function createConfig(): SignalRConfiguration {
  const c = new SignalRConfiguration();
  c.hubName = 'ChatHub';
  // c.qs = { message: 'msg' };
  c.url = 'https://localhost:44315';
  c.logging = true;

  // >= v5.0.0
  c.executeEventsInZone = true; // optional, default is true
  c.executeErrorsInZone = false; // optional, default is false
  c.executeStatusChangeInZone = true; // optional, default is true
  return c;
}

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    SignUpComponent,
    UserComponent,
    SignInComponent,
    PdftesterComponent,
    UserHierarchyComponent,
    ForbiddenComponent,
    StatusTrackerComponent,
    StatusBarComponent,
    DialogBoxComponent,
    DialogStatusTestComponent,
    UserAssociationComponent,
    UserInviteComponent,
    UserInviteWelcomeComponent,
    CompanyManagementComponent,
    DialogCompanyEditComponent,
    UserManagementComponent,
    DialogUserEditComponent
    ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    SignalRModule.forRoot(createConfig),
    NgbModule,
    HttpClientModule,
    ToastrModule.forRoot(),
    BrowserAnimationsModule,
    FormsModule,
    MaterialModule,
    //PdfViewerModule
    PdfJsViewerModule,
    FontAwesomeModule
  ],
  entryComponents:[
    DialogBoxComponent
  ],
  providers: [UserService, AuthGuard, {
    provide: HTTP_INTERCEPTORS,
    useClass: AuthInterceptor,
    multi: true
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }
