export class User {
    UserName: string;
    Password: string;
    Email: string;
    FirstName: string;
    LastName: string;
    Roles: string[];
    UserId: string;
    AgentLicenseNumber: string;
    Company: Company;
}

export class UserInvite {
    Email: string;
    Password: string;
    FirstName: string;
    LastName: string;
    RoleId: string;
    EmailBody: string;
}

export class InviteWelcome {
    InviterName: string;
    InviteeFirstName: string;
    InviteeLastName: string;
    InviteeEmail: string;
    RoleId: string;
    Role: string;
    Company: Company
}

export class Company {
    CompanyId: number;
    Name: string;
    Address1: string;
    Address2: string;
    City: string;
    State: string;
    Zip: string;
}

export interface ApiResponse<T> {
    IsSuccessful: boolean;
    Message?: string;
    Data?: T;
}