import { Component, OnInit } from '@angular/core';
import { UserInvite } from 'src/app/models/models';
import { NgForm, FormControl } from '@angular/forms';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-user-invite',
  templateUrl: './user-invite.component.html',
  styleUrls: ['./user-invite.component.scss']
})
export class UserInviteComponent implements OnInit {

  public userInvite: UserInvite;
  public emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  public roles: any[];
  public selectedRole: string;
  public rolesForm = new FormControl();
  public defaultEmailBody: string = `YO!YO!YO!...
  
  Join BAC Agent!!!
  
  XOXOXO
  - Your Bestie`;

  constructor(
    private userService: UserService,
    private toastr: ToastrService
  ) { }

  ngOnInit(): void {
    this.resetForm();
    this.userService.getAllRoles().subscribe((x: any) => {
      x.forEach(y => y.selected = false);
      this.roles = x;
    })
  }

  resetForm(form?: NgForm){
    if(form != null){
      form.reset();
    }

    this.userInvite = {
      Email: '',
      FirstName: '',
      LastName: '',
      EmailBody: this.defaultEmailBody,
      Password: '',
      RoleId: ''
    };

    if (this.roles) {
      this.roles.map(x => x.selected = false);
    }

  }

  OnSubmit(form: NgForm) {
   // var roles = this.roles.filter(x => x.selected).map(y => y.Name);

    form.value.RoleId = this.rolesForm.value;

    this.userService.inviteUser(form.value)
      .subscribe((data: any) => {
        if (data.Succeeded == true) {
          this.resetForm(form);
          this.toastr.success('User registration successful');
        }
        else
          this.toastr.error(data.Errors[0]);
      });
  }

  updateSelectedRoles(index) {
    this.roles[index].selected = !this.roles[index].selected;
  }

}
